:root {
  --font-family: "NeutralFace", sans-serif;
  --second-family: "Roboto", sans-serif;
  --content-width: 1380px;
  --container-offset: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --offset: 30px;
  --light-color: #fff;
  --main-color: #0b3825;
  --dark-color: rgb(4, 37, 26);
  --form-color: #faf1d9;
  --hover-color: #eed494;
  --active-color: #cbad62;
  --focus-color: #eed494;
  --accent-color: #dec37d;
}

@font-face {
  font-family: "NeutralFace";
  src: url("../fonts/../fonts/NeutralFaceRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "NeutralFace";
  src: url("../fonts/../fonts/NeutralFaceBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "NeutralFace";
  src: url("../fonts/../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  position: relative;
  overflow-x: clip;
  margin: 0;
  padding: 0;
  border: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
  color: var(--light-color);
  background-color: var(--main-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn {
  display: inline-block;
  position: relative;
  padding-bottom: 4px;
  text-decoration: none;
}

[class*=__container] {
  max-width: 1380px;
  margin: 0 auto;
  padding: 0 40px;
}

@media (max-width: 768px) {
  [class*=__container] {
    padding: 0 20px;
  }
}
.btn {
  display: inline-block;
  position: relative;
  padding-bottom: 4px;
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  column-gap: var(--offset);
  grid-template-columns: repeat(12, 1fr);
}
@media (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.section {
  padding-top: 120px;
}
@media (max-width: 1190px) {
