.title {
  margin: 0;
  margin-bottom: 30px;
  font-family: var(--font-family);
  font-size: 48px;
  line-height: 50px;
  color: var(--light-color);
  text-transform: uppercase;
}
@media (max-width: 1190px) {
  .title {
    font-size: 40px;
    line-height: 48px;
  }
}
@media (max-width: 1024px) {
