    font-size: 44px;
    line-height: 48px;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 32px;
  }
}

.btn {
  color: var(--light-color);
  font-size: 18px;
}

.btn::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #cbd5c4;
  transition: transform 0.3s linear;
}

.btn::before:hover {
  transform: translate 0.3s linear;
}

.burger {
  position: relative;
  border: none;
  padding: 0;
  width: 25px;
  height: 25px;
  background-color: transparent;
  cursor: pointer;
  display: none;
  z-index: 5;
  color: var(--light-color);
  border: 0;
}
.burger__line, .burger__line::before, .burger__line::after {
  display: block;
  content: "";
  background-color: #fff;
  height: 3px;
  width: 28px;
  border-radius: 4px;
  transition: all ease-in-out 0.3s;
}
.burger__line::before {
  transform: translateY(-8px);
}
.burger__line::after {
  transform: translateY(5px);
}
@media (max-width: 768px) {
  .burger {
    display: block;
  }
}

@media (hover: none) and (pointer: coarse) {
  .styles__img:hover {
    transform: none;
    color: inherit;
  }
}
@media (any-hover) {
  .btn:hover::before {
    transform: scaleX(0.9) scaleY(2);
  }
  .burger-btn:hover {
    cursor: pointer;
  }
}
.gallery {
  display: none;
}

